import React from 'react';
import {Container, Image} from "react-bootstrap";
import {Link} from "react-router-dom";

function WIP() {

    return (
        <>
            <Container className="justify-content-center" style={{marginTop: "3em"}}>
                <div className="text-center">
                    <h1> Hey there! This site is currently being mega-reworked :) </h1>
                    <br/>
                    <h4> Dubs* will keep you company while you're here</h4>
                    <Link to="/resume">
                        <Image width="100%" src="assets/dubsgif.gif" rounded/>
                    </Link>
                    <h5> Psst, if you're looking for my resume, you can click on dubs!</h5>
                </div>
            </Container>
            <h5 style={{position: "fixed", bottom: 0, right: 0, marginRight: "1em"}}>
                <text style={{fontSize: "2em"}}>*</text> Dubs 2, (may <i style={{fontFamily: "Times New Roman"}}>His Boundlessness Dubs I</i> rest in peace)
            </h5>
        </>
    );
}

export default WIP;